.row {
    margin-left: 20px;
    color: white;
}
.row_posters {
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 20px;
}
.row_posters::-webkit-scrollbar{
    display: none;
}
.row_poster {
    max-height: 100px;
    margin-right: 10px;
    cursor: pointer;
    object-fit: contain;
    width: 100%;
    transition: 450ms;
}
.row_poster:hover {
    transform: scale(1.08);
    opacity: 1;
}
.row_posterLarge {
    max-height: 250px;
}
.row_posterLarge:hover {
    transform: scale(1.09);
    opacity: 1;
}

@media (max-width: 480px) {
    .row > h2 {
        text-align: center;
    }
    .row_posterLarge {
        min-height: 200px;
    }
    .row_poster {
        max-height: 80px;
    }
}