.nav {
    position: fixed;
    top: 0;
    padding: 20px;
    width: 100%;
    height: 30px;
    z-index: 1;
    transition-timing-function: ease-in;
    transition: all 0.5s;
}
.nav_black {
    background-color: #111;
}
.nav_contents {
    display: flex;
    justify-content: space-between;
}
.nav_logo {
    position: fixed;
    top: 9px;
    left: 0;
    height: 50px;
    cursor: pointer;
    object-fit: contain;
    padding-left: 20px;
}
.nav_avatar {
    position: fixed;
    top: 9px;
    right: 20px;
    height: 50px;
    cursor: pointer;
}